<template>
  <div class="">
    <h1>{{ id? '': '' }}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-input v-model="model.username"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input type="text" v-model="model.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'AdminUserEdit',
  props: {
    id: {}  // ID
  },
  data () {
    return {
      model: {}, // 
    }
  },
  mounted () {
    // ID
    this.id && this.fetch()
  },
  methods: {
    // 
    async fetch () {
      let res = await this.$http.get(`rest/admin_users/${this.id}`)
      this.model = res.data
    },
    // 
    async save () {
      // ID
      if (this.id) {
        // 
        await this.$http.put(`rest/admin_users/${this.id}`, this.model)
      } else {
        // 
        await this.$http.post('rest/admin_users', this.model)
      }

      // 
      this.$message({
        type: 'success',
        message: ``
      })
      // 
      this.$router.push('/admin_users/list')
    },
  },
  // watch: {
  //   // ID
  //   id () {
  //     this.model = {}
  //   }
  // }
}
</script>

