<template>
  <div class="">
    <h1>{{ id? '': '' }}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button size="small" @click="model.items.push({})"><i class="el-icon-plus"></i> </el-button>
        <el-row type="flex" style="flex-wrap: wrap;">
          <el-col :md="24" v-for="(item, index) in model.items" :key="index">
            <el-form-item label="">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="" style="margin-top: .5rem;">
              <el-upload class="avatar-uploader" :action="uploadUrl" :headers="getAuthHeaders()" :show-file-list="false" :on-success="res => $set(item, 'image', res.url)">
                <img v-if="item.image" :src="item.image" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item>
              <el-button size="small" type="danger" @click="model.items.splice(index, 1)"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'AdEdit',
  props: {
    id: {}  // ID
  },
  data () {
    return {
      model: {
        name: ``,
        items: [],
      },
    }
  },
  mounted () {
    // ID
    this.id && this.fetch()
  },
  methods: {
    // 
    afterUpload (res) {
      // 
      // this.$set(this.model, 'avatar', res.url)
      this.model.image = res.url
    },
    // 
    async fetch () {
      let res = await this.$http.get(`rest/ads/${this.id}`)
      // this.model = res.data
      this.model = Object.assign({}, this.model, res.data)
    },
    // 
    async save () {
      // ID
      if (this.id) {
        // 
        await this.$http.put(`rest/ads/${this.id}`, this.model)
      } else {
        // 
        await this.$http.post('rest/ads', this.model)
      }

      // 
      this.$message({
        type: 'success',
        message: ``
      })
      // 
      this.$router.push('/ads/list')
    },
  },
  // watch: {
  //   // ID
  //   id () {
  //     this.model = {}
  //   }
  // }
}
</script>

