<template>
  <div class="">
    <h1>{{ id? '': '' }}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-select v-model="model.categories" multiple>
          <el-option v-for="item in categories" :key="item._id" :label="item.name" :value="item._id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="">
        <vue-editor v-model="model.body" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";

export default {
  name: 'ArticleEdit',
  props: {
    id: {}  // ID
  },
  components: {
    VueEditor
  },
  data () {
    return {
      model: {}, // 
      categories: [], //
    }
  },
  mounted () {
    // 
    this.fetchCategories()
    // ID
    this.id && this.fetch()
  },
  methods: {
    // vue2-editor 
    async handleImageAdded (file, Editor, cursorLocation, resetUploader) {
      let formData = new FormData();
      formData.append("file", file);

      let res = await this.$http.post('upload', formData)
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
    },
    // 
    async fetchCategories () {
      let res = await this.$http.get(`rest/categories`)
      this.categories = res.data
    },
    // 
    async fetch () {
      let res = await this.$http.get(`rest/articles/${this.id}`)
      this.model = res.data
    },
    // 
    async save () {
      // ID
      if (this.id) {
        // 
        await this.$http.put(`rest/articles/${this.id}`, this.model)
      } else {
        // 
        await this.$http.post('rest/articles', this.model)
      }

      // 
      this.$message({
        type: 'success',
        message: ``
      })
      // 
      this.$router.push('/articles/list')
    },
  },
  // watch: {
  //   // ID
  //   id () {
  //     this.model = {}
  //   }
  // }
}
</script>

