<template>
  <div class="">
    <h1>{{ id? '': '' }}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-tabs value="basic" type="border-card">
        <el-tab-pane label="" name="basic">
          <el-form-item label="">
            <el-input v-model="model.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-upload class="avatar-uploader" :action="uploadUrl" :headers="getAuthHeaders()" :show-file-list="false" :on-success="res => $set(model, 'avatar', res.url)">
              <img v-if="model.avatar" :src="model.avatar" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="banner">
            <el-upload class="avatar-uploader" :action="uploadUrl" :headers="getAuthHeaders()" :show-file-list="false" :on-success="res => $set(model, 'banner', res.url)">
              <img v-if="model.banner" :src="model.banner" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.title"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.categories" multiple>
              <el-option v-for="item in categories" :key="item._id" :label="item.name" :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-rate v-model="model.scores.difficult" :max="9" show-score style="margin-top: .6rem;"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate v-model="model.scores.skills" :max="9" show-score style="margin-top: .6rem;"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate v-model="model.scores.attack" :max="9" show-score style="margin-top: .6rem;"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate v-model="model.scores.survive" :max="9" show-score style="margin-top: .6rem;"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.items1" multiple>
              <el-option v-for="item in items" :key="item._id" :label="item.name" :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.items2" multiple>
              <el-option v-for="item in items" :key="item._id" :label="item.name" :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.usageTips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.battleTips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.teamTips"></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name="skills">
          <el-button size="small" @click="model.skills.push({})"><i class="el-icon-plus"></i> </el-button>
          <el-row type="flex" style="flex-wrap: wrap;">
            <el-col :md="12" v-for="(item, index) in model.skills" :key="index">
              <el-form-item label="">
                <el-input v-model="item.name"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-upload class="avatar-uploader" :action="uploadUrl" :headers="getAuthHeaders()" :show-file-list="false" :on-success="res => $set(item, 'icon', res.url)">
                  <img v-if="item.icon" :src="item.icon" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.delay"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.cost"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input type="textarea" v-model="item.description"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input type="textarea" v-model="item.tips"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button size="small" type="danger" @click="model.skills.splice(index, 1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="" name="partners">
          <el-button size="small" @click="model.partners.push({})"><i class="el-icon-plus"></i> </el-button>
          <el-row type="flex" style="flex-wrap: wrap;">
            <el-col :md="12" v-for="(item, index) in model.partners" :key="index">
              <el-form-item label="">
                <el-select filterable v-model="item.hero">
                  <el-option v-for="hero in heroes" :key="hero._id" :value="hero._id" :label="hero.name"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input type="textarea" v-model="item.description"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button size="small" type="danger" @click="model.partners.splice(index, 1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>
      <el-form-item style="margin-top: 1rem;">
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'HeroEdit',
  props: {
    id: {}  // ID
  },
  data () {
    return {
      categories: [], // 
      items: [], // 
      heros: [], // 
      model: {
        name: ``,
        avatar: ``,
        banner: ``,
        title: ``,
        categories: ``,
        scores: {
          difficult: 0,
          skills: 0,
          attack: 0,
          survive: 0,
        },
        skills: [],
        items1: [],
        items2: [],
        usageTips: ``,
        battleTips: ``,
        teamTips: ``,
        partners: [],
      }, // 
    }
  },
  mounted () {
    // 
    this.fetchCategories()
    // 
    this.fetchItems()
    // 
    this.fetchHeroes()
    // ID
    this.id && this.fetch()
  },
  methods: {
    // 
    // afterUpload (res) {
      // 
      // this.$set(this.model, 'avatar', res.url)
      // this.model.avatar = res.url
    // },
    // 
    async fetch () {
      let res = await this.$http.get(`rest/heros/${this.id}`)
      // this.model = res.data
      this.model = Object.assign({}, this.model, res.data)
    },
    // 
    async fetchCategories () {
      let res = await this.$http.get('rest/categories')
      this.categories = res.data
    },
    // 
    async fetchItems () {
      let res = await this.$http.get('rest/items')
      this.items = res.data
    },
    // 
    async fetchHeroes () {
      let res = await this.$http.get('rest/heroes')
      this.heroes = res.data
    },
    // 
    async save () {
      // ID
      if (this.id) {
        // 
        await this.$http.put(`rest/heros/${this.id}`, this.model)
      } else {
        // 
        await this.$http.post('rest/heros', this.model)
      }

      // 
      this.$message({
        type: 'success',
        message: ``
      })
      // 
      this.$router.push('/heros/list')
    },
  },
  // watch: {
  //   // ID
  //   id () {
  //     this.model = {}
  //   }
  // }
}
</script>

