<template>
  <div class="">
    <h1>{{ id? '': '' }}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-upload class="avatar-uploader" :action="uploadUrl" :headers="getAuthHeaders()" :show-file-list="false" :on-success="afterUpload">
          <img v-if="model.icon" :src="model.icon" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'ItemEdit',
  props: {
    id: {}  // ID
  },
  data () {
    return {
      model: {}, // 
    }
  },
  mounted () {
    // ID
    this.id && this.fetch()
  },
  methods: {
    // 
    afterUpload (res) {
      // 
      this.$set(this.model, 'icon', res.url)
      // this.model.icon = res.url
    },
    // 
    async fetch () {
      let res = await this.$http.get(`rest/items/${this.id}`)
      this.model = res.data
    },
    // 
    async save () {
      // ID
      if (this.id) {
        // 
        await this.$http.put(`rest/items/${this.id}`, this.model)
      } else {
        // 
        await this.$http.post('rest/items', this.model)
      }

      // 
      this.$message({
        type: 'success',
        message: ``
      })
      // 
      this.$router.push('/items/list')
    },
  },
  // watch: {
  //   // ID
  //   id () {
  //     this.model = {}
  //   }
  // }
}
</script>

