<template>
  <div class="">
    <h1></h1>
    <el-table :data="dataList">
      <el-table-column prop="_id" label="ID" width="220"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column prop="avatar" label="">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" alt="" style="height: 3rem;">
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="$router.push(`/heros/edit/${scope.row._id}`)"></el-button>
          <el-button type="text" size="small" @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination v-if="items.length > 0" @current-change="currentChange" :current-page="nowpage" :page-size="pageSize" :total="items.length"></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'HeroList',
  data () {
    return {
      items: [], // 
      dataList: [],
      nowpage: 1, //
      pageSize: 10, //
    }
  },
  mounted () {
    this.fetch()
  },
  methods: {
    // 
    async fetch () {
      let res = await this.$http.get('rest/heros')
      this.items = res.data
      // 
      this.currentChange(1)
    },
    // 
    currentChange (page) {
      this.nowpage = page
      let start = (page - 1) * this.pageSize
      let end = start + this.pageSize
      this.dataList = this.items.slice(start, end)
    },
    // 
    async remove (row) {
      this.$confirm(` "${row.name}"`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        closeOnClickModal: false,
      }).then(async () => {
        let res = await this.$http.delete(`rest/heros/${row._id}`)
        if (res.data.success) {
          this.$message({
            type: 'success',
            message: '!'
          })
          this.fetch()
        } else {
          this.$message({
            type: 'error',
            message: '!'
          })
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

